"use client";
import { useEffect, useState } from "react";
import api from "../api/authapi"; // tumhara axios instance

export default function FeedScreen() {
    const [posts, setPosts] = useState([]);
    const [selectedPost, setSelectedPost] = useState(null);
    const [commentText, setCommentText] = useState("");

    // âœ… Backend se posts fetch
    const fetchPosts = async () => {
        try {
            let res = await api.get("/userget");
            setPosts(res.data);
            console.log("Posts:", res.data);
        } catch (error) {
            console.log("Fetch error", error.message);
        }
    };

    useEffect(() => {
        fetchPosts();
    }, []);

    // âœ… Like button
    const handleLike = async (postId) => {
        let userId = localStorage.getItem("id");
        try {
            let res = await api.post("/like", { userId, postId });
            console.log("Like response:", res.data);

            // UI update
            setPosts((prev) =>
                prev.map((p) =>
                    p._id === postId ? { ...p, likes: res.data.likes } : p
                )
            );
        } catch (error) {
            console.log(error);
        }
    };

    // âœ… Comment button
    const handleComment = async (postId) => {
        if (!commentText.trim()) return;

        try {
            // Backend ko bhi bhejna chaho to
            // await api.post("/comment", { userId, postId, text: commentText });

            setPosts((prev) =>
                prev.map((p) =>
                    p._id === postId
                        ? { ...p, comments: [...p.comments, { text: commentText }] }
                        : p
                )
            );
            setCommentText("");
            setSelectedPost(null);
        } catch (error) {
            console.log(error);
        }
    };

    return (
        <div className="bg-gray-100 min-h-screen p-4">
            {/* New Post Button */}
            <button
                onClick={() => (window.location.href = "/post")}
                className="bg-blue-500 text-white px-4 py-2 rounded-lg mb-4"
            >
                âž• New Post
            </button>

            {/* Posts */}
            <div className="space-y-4">
                {posts.map((item) => (
                    <div key={item._id} className="bg-white p-4 rounded-lg shadow">
                        <p className="font-bold">ðŸ‘¤ {item.user}</p>
                        <p className="mb-2">{item.content}</p>

                        {item.image && (
                            <img
                                src={`http://localhost:5000${item.image}`}
                                alt="post"
                                className="w-full h-60 object-cover rounded-md mb-2"
                            />
                        )}

                        {/* Likes + Comments */}
                        <div className="flex justify-between">
                            <button onClick={() => handleLike(item._id)}>
                                ðŸ’– {item.likes.length}
                            </button>
                            <button onClick={() => setSelectedPost(item)}>
                                ðŸ’¬ {item.comments.length}
                            </button>
                        </div>
                    </div>
                ))}
            </div>

            {/* Comment Modal */}
            {selectedPost && (
                <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center">
                    <div className="bg-white p-6 rounded-lg w-96">
                        <h2 className="text-lg font-bold mb-3">Comments</h2>

                        {selectedPost.comments.map((c, i) => (
                            <p key={i} className="mb-1">â€¢ {c.text}</p>
                        ))}

                        <input
                            value={commentText}
                            onChange={(e) => setCommentText(e.target.value)}
                            placeholder="Write a comment..."
                            className="border p-2 w-full mb-3 rounded"
                        />
                        <div className="flex gap-2">
                            <button
                                onClick={() => handleComment(selectedPost._id)}
                                className="bg-green-500 text-white px-3 py-1 rounded"
                            >
                                Post Comment
                            </button>
                            <button
                                onClick={() => setSelectedPost(null)}
                                className="bg-red-500 text-white px-3 py-1 rounded"
                            >
                                Close
                            </button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
}
